.preloader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  z-index: 999999;
  justify-content: center;
  background: #fff;
}

.loading {
  position: absolute;
  width: 60px;
  height: 60px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.loading .box {
  width: 30px;
  height: 30px;
  position: absolute;
}
.loading .box-1 {
  background-color: #057689;
  top: 0;
  left: 0;
  z-index: 1;
  animation: box-1_move 1.8s cubic-bezier(0.6, 0.01, 0.4, 1) infinite;
}
.loading .box-2 {
  background-color: #FFBE00;
  top: 0;
  right: 0;
  animation: box-2_move 1.8s cubic-bezier(0.6, 0.01, 0.4, 1) infinite;
}
.loading .box-3 {
  background-color: #020C17;
  bottom: 0;
  right: 0;
  z-index: 1;
  animation: box-3_move 1.8s cubic-bezier(0.6, 0.01, 0.4, 1) infinite;
}
.loading .box-4 {
  background-color: #01373C;
  bottom: 0;
  left: 0;
  animation: box-4_move 1.8s cubic-bezier(0.6, 0.01, 0.4, 1) infinite;
}

@keyframes box-1_move {
  0%, 100% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(0, 30px);
  }
  50% {
    transform: translate(30px, 30px);
  }
  75% {
    transform: translate(30px, 0);
  }
}
@keyframes box-2_move {
  0%, 100% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(-30px, 0);
  }
  50% {
    transform: translate(-30px, 30px);
  }
  75% {
    transform: translate(0, 30px);
  }
}
@keyframes box-3_move {
  0%, 100% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(0, -30px);
  }
  50% {
    transform: translate(-30px, -30px);
  }
  75% {
    transform: translate(-30px, 0);
  }
}
@keyframes box-4_move {
  0%, 100% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(30px, 0);
  }
  50% {
    transform: translate(30px, -30px);
  }
  75% {
    transform: translate(0, -30px);
  }
}
